/* src/app/app.scss */
:host {
  display: block;
  height: 100vh;
}
:host .app-container {
  display: flex;
  height: 100%;
}
:host .nav-rail {
  width: 72px;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border-right: 1px solid var(--mat-sidenav-container-divider-color);
  background: var(--mat-sidenav-container-background-color);
  background: var(--mat-sys-surface-dim);
  background: var(--mat-sys-surface-container-lowest);
  z-index: 2;
}
:host .nav-rail .nav-rail-top {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 16px 0;
}
:host .nav-rail .nav-rail-top .logo {
  padding: 8px;
  margin-bottom: 24px;
}
:host .nav-rail .nav-rail-top .logo .logo-image {
  height: 24px;
  width: auto;
  max-width: 100%;
}
:host .nav-rail .nav-rail-top .nav-rail-items {
  display: flex;
  flex-direction: column;
  gap: 8px;
}
:host .nav-rail .nav-rail-top .nav-rail-items .nav-rail-item {
  width: 48px;
  height: 48px;
  margin: 0 12px;
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  color: var(--mat-sys-on-surface);
  transition:
    background-color 180ms ease,
    color 180ms ease,
    transform 120ms ease;
  cursor: pointer;
}
:host .nav-rail .nav-rail-top .nav-rail-items .nav-rail-item mat-icon,
:host .nav-rail .nav-rail-top .nav-rail-items .nav-rail-item .mat-icon {
  font-size: 20px;
  line-height: 1;
  transition:
    color 180ms ease,
    opacity 180ms ease,
    transform 120ms ease;
  color: inherit;
  opacity: 0.9;
}
:host .nav-rail .nav-rail-top .nav-rail-items .nav-rail-item:not(.active):hover {
  background-color: rgba(0, 0, 0, 0.04);
  transform: translateY(-1px);
}
:host .nav-rail .nav-rail-top .nav-rail-items .nav-rail-item.active {
  background-color: var(--mat-sys-primary-container);
  color: var(--mat-sys-on-primary-container);
}
:host .nav-rail .nav-rail-top .nav-rail-items .nav-rail-item.active::before {
  content: "";
  position: absolute;
  left: -12px;
  width: 6px;
  height: 28px;
  border-radius: 4px;
  background: var(--mat-sys-primary);
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.12);
  transition: background-color 180ms ease, transform 160ms ease;
}
:host .nav-rail .nav-rail-top .nav-rail-items .nav-rail-item.theme-toggle:not(.active):hover {
  background-color: rgba(0, 0, 0, 0.04);
}
:host .nav-rail .nav-rail-top .nav-rail-items .nav-rail-item.theme-toggle.active {
  background-color: var(--mat-sys-primary-container);
  color: var(--mat-sys-on-primary-container);
}
:host .nav-rail .nav-rail-bottom {
  padding: 16px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 16px;
}
:host .nav-rail .nav-rail-bottom .theme-toggle {
  width: 48px;
  height: 48px;
  margin: 0 12px;
  border-radius: 16px;
  transition: background-color 200ms ease;
}
:host .nav-rail .nav-rail-bottom .theme-toggle:hover {
  background-color: var(--mat-sidenav-container-divider-color);
}
:host .nav-rail .nav-rail-bottom .theme-toggle mat-icon {
  font-size: 20px;
  height: 20px;
  width: 20px;
  opacity: 0.7;
  transition: opacity 200ms ease;
}
:host .nav-rail .nav-rail-bottom .theme-toggle:hover mat-icon {
  opacity: 1;
}
:host .nav-rail .nav-rail-bottom button.active {
  background-color: var(--mat-sidenav-container-divider-color);
}
:host mat-sidenav-container {
  flex: 1;
  height: 100%;
}
:host mat-sidenav-container mat-sidenav {
  width: 256px;
  border-right: 1px solid var(--mat-sidenav-container-divider-color);
}
:host mat-sidenav-container mat-sidenav mat-nav-list {
  padding-top: 16px;
}
:host mat-sidenav-container .customize-sidenav {
  padding: 16px;
  box-sizing: border-box;
}
:host mat-sidenav-container .customize-sidenav .sidenav-header {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 12px;
}
:host mat-sidenav-container .customize-sidenav .sidenav-header mat-icon {
  font-size: 20px;
}
:host mat-sidenav-container .customize-sidenav .sidenav-header h4 {
  margin: 0;
  font-size: 1rem;
}
:host mat-sidenav-container .customize-sidenav .sidenav-section {
  margin-bottom: 16px;
}
:host mat-sidenav-container .customize-sidenav .sidenav-section h5 {
  margin: 8px 0;
}
:host mat-sidenav-container .customize-sidenav .sidenav-section h6 {
  margin: 6px 0;
  font-weight: 500;
}
:host mat-sidenav-container .content-container {
  padding: 24px;
}
/*# sourceMappingURL=app.css.map */
